.profile{
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 100%; 
    margin-bottom: 15px;
    background-color: rgb(161, 174, 185); 
    border-radius: 5px;
    border-color: rgb(180, 233, 233);
    border: 5px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.description{
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 100%;   
}

.avatar{
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    width: 300px;
    height: 240px;
}

.name{
    font-size: 20px;
    margin-top: 0px;
    margin-bottom: 10px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 700;
    text-align: center;
}

.tag{
    margin-top: 0px;
    margin-bottom: 10px;
    font-size: 15px;
    font-family: 'Times New Roman', Times, serif;
    text-align: center; 
    color: rgb(25, 25, 218);
}

.location{
    margin-bottom: 10px;
    font-size: 15px;
    font-family: 'Times New Roman', Times, serif;
    text-align: center; 
}

.stats{
    background-color: rgba(29, 165, 170, 0.61);
    display: flex;
    margin-left: auto;
    margin-bottom: auto;
    margin-bottom: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 0px;
    list-style: none;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
}

.item{
    color: rgb(128, 89, 31);
}

.label{
    padding-right: 2px;
    font-size: 15px;
    font-weight: 700;
    font-family: 'Times New Roman', Times, serif;
    text-align: center;
}

.quantity{
    font-size: 15px;
    font-family: 'Times New Roman', Times, serif;
    text-align: center;
}